  // })

  // $("#num1").change(function() {
  //   console.log($("#num1").val());
  // })

  useEffect(() => {
    let _totalPrice = localStorage.getItem('totalPrice');
    let _name = localStorage.getItem('name');
    // console.log('totalPrice', parseFloat(_totalPrice).toFixed(2));
    setTotalPrice(parseFloat(_totalPrice).toFixed(2));
    setName(_name);

    async function getToken() {
      try {
        // Get a client token for authorization from your server
        const response = await axios.get(
          `/api/braintree/v1/getToken`
        );
        const _clientToken = response.data.clientToken;
        setClientToken(_clientToken);
      } catch (err) {
        console.error(err);
      }
    }

    getToken();
  
  }, []);

