  const buy = async () => {
    try {
      // Send the nonce to your server
      const { nonce } = await instance.requestPaymentMethod();
      const response = await axios.post(
        `/api/braintree/v1/sandbox`,
        nonce
      );
      console.log(response);
    } catch (err) {
      console.error(err);
    }
  }

